import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import * as io from 'socket.io-client'
import { Observable } from 'rxjs';

//--------------------------------------------------------
@Injectable({
  providedIn: 'root'
})

//--------------------------------------------------------
export class FilesService {


//Headers
header = new HttpHeaders({
  'Content-Type':'application/json',
  'Access-Control-Allow-Origin':'*'
});

//--------- MAIN URL -----------------------------
private _url: string = "http://127.0.0.1:8040/";
private socket;

//--------------------------------------------------------

constructor( private http: HttpClient ) { this.socket = io(this._url) }





// ADMISSION ROUTER
//-------------Post Admission --------------------------------------
admitPatient( record : any ) {
  return this.http.post<any>(this._url + 'admission', record, {headers : this.header});
}












// EXAMINATION ROUTER
//------------- List Admitted Patients ---------------------------
listAdmitPatient() {
  var socket = io()
  return Observable.create((observer) =>{
    socket.on('/admission', data => {
      observer.next(data);
    })
  });
}

//-------------- Post Open Admission File
openAdmFile( record : any ) {
  return this.http.post<any>(this._url + "openadmissionfile", record, {headers : this.header});
}

//-------------- Examination on Reload ------------------
examinationReload() {
  return this.http.delete<any>(this._url + "examinationreload", {headers : this.header});
}

//--------------- Update Examination form -------------------
updateExamForm() {
  var socket = io()
  return Observable.create((observer) =>{
    socket.on('/updateexamform', data => {
      observer.next(data);
    })
  });
}

//--------------- Delete File ----------------------------
deleteFile(){
  return this.http.delete<any>(this._url + "deletefile", {headers : this.header});
}


//--------------- To Lab ----------------------------
tolab( record : any ){
  return this.http.post<any>(this._url + "tolab", record, {headers : this.header});
}

//------------- List Patients from lab ---------------------------
listPatientfromLab() {
  var socket = io()
  return Observable.create((observer) =>{
    socket.on('/listfromlab', data => {
      observer.next(data);
    })
  });
}
//-------------- open File from Lab --------------------
openFilefromLab( record : any ) {
  return this.http.post<any>(this._url + "openfilefromlab", record, {headers : this.header});
}

//--------------- To Xray ----------------------------
toxray( record : any ){
  return this.http.post<any>(this._url + "toxray", record, {headers : this.header});
}

//------------- List  Patients from xray ---------------------------
listPatientfromXray() {
  var socket = io()
  return Observable.create((observer) =>{
    socket.on('/listfromxray', data => {
      observer.next(data);
    })
  });
}
//-------------- Open File from Xray ---------------------------------
openFilefromXray( record : any ) {
  return this.http.post<any>(this._url + "openfilefromxray", record, {headers : this.header});
}

//--------------- To Pharmacy ----------------------------
topharmacy( record : any ){
  return this.http.post<any>(this._url + "topharmacy", record, {headers : this.header});
}

topharm( record : any ){
  return this.http.post<any>(this._url + "topharm", record, {headers : this.header});
}













// LAB ROUTER
//------------- List ToLab Patients ---------------------------
listLabPatient() {
  var socket = io()
  return Observable.create((observer) =>{
    socket.on('/listtolab', data => {
      observer.next(data);
    })
  });
}

//-------------- Open LabFile
openLabFile( record : any ) {
  return this.http.post<any>(this._url + "openlabfile", record, {headers : this.header});
}

//-------------- Lab on Reload ------------------
labReload() {
  return this.http.delete<any>(this._url + "labreload", {headers : this.header});
}
//--------------- Update Lab form -------------------
updateLabForm() {
  var socket = io()
  return Observable.create((observer) =>{
    socket.on('/updatelabform', data => {
      observer.next(data);
    })
  });
}
//--------------- Lab to exam ----------------------------
labToExam( record : any ){
  return this.http.post<any>(this._url + "labtoexam", record, {headers : this.header});
}


















// XRAY ROUTER
//------------- List ToLab Patients ---------------------------
listXrayPatient() {
  var socket = io()
  return Observable.create((observer) =>{
    socket.on('/listtoxray', data => {
      observer.next(data);
    })
  });
}
//-------------- Open Xray File
openXrayFile( record : any ) {
  return this.http.post<any>(this._url + "openxrayfile", record, {headers : this.header});
}

//-------------- Xray on Reload ------------------
xrayReload() {
  return this.http.delete<any>(this._url + "xrayreload", {headers : this.header});
}

//--------------- Update Xray form -------------------
updateXrayForm() {
  var socket = io()
  return Observable.create((observer) =>{
    socket.on('/updatexrayform', data => {
      observer.next(data);
    })
  });
}

//--------------- Xray to exam ----------------------------
xrayToExam( record : any ){
  return this.http.post<any>(this._url + "xraytoexam", record, {headers : this.header});
}














// PHARMACY ROUTER 
//------------- List Pharmacy Patients ---------------------------
listPharmacyPatient() {
  var socket = io()
  return Observable.create((observer) =>{
    socket.on('/listpharmacy', data => {
      observer.next(data);
    })
  });
}
//-------------- Open Pharmacy File
openPharmacyFile( record : any ) {
  return this.http.post<any>(this._url + "openpharmacyfile", record, {headers : this.header});
}

//-------------- Pharmacy on Reload ------------------
pharmacyReload() {
  return this.http.delete<any>(this._url + "pharmacyreload", {headers : this.header});
}

//--------------- Update Xray form -------------------
updatePharmacyForm() {
  var socket = io()
  return Observable.create((observer) =>{
    socket.on('/updatepharmacyform', data => {
      observer.next(data);
    })
  });
}

//--------------- save File ----------------------------
saveFile( record : any ){
  return this.http.post<any>(this._url + "savefile", record, {headers : this.header});
}





//=============== THE END ==============================================
}
