import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders} from "@angular/common/http"; 


//---------------------------------
@Injectable({
  providedIn: 'root'
})

//--------------------------------------------------------
export class UsersService {

//--------------------------------------------------------

  constructor( private http: HttpClient) { }

//Headers

header = new HttpHeaders({
  'Content-Type':'application/json',
  'Access-Control-Allow-Origin':'*'
});

//--------- MAIN URL -----------------------------
_url: string = "http://127.0.0.1:8040/";


//-------------Post Clinician Registration -----------------------------
regClinician( record : any ) {
  return this.http.post<any>(this._url + "regclinician", record, {headers : this.header})
}

//-------------Post Clinician Login -----------------------------
logClinician( record : any ) {
  return this.http.post<any>(this._url + "logclinician", record, {headers : this.header})
  
  }

//-------------Post Admin Registration -----------------------------
regAdmin( record : any ) {
return this.http.post<any>(this._url + "regadmin", record, {headers : this.header})
}

//-------------Post Clinician Login -----------------------------
logAdmin( record : any ) {
return this.http.post<any>(this._url + "logadmin", record, {headers : this.header})
}


//=============== THE END ==============================================
}
//--------------------------------------------------------
