import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from "@angular/common/http";
import * as io from 'socket.io-client'
import { Observable } from 'rxjs';

//-------------------------------------------------
@Injectable({
  providedIn: 'root'
})

//---------------------------------------------------
export class StatsService {

 

//Headers
header = new HttpHeaders({
  'Content-Type':'application/json',
  'Access-Control-Allow-Origin':'*'
});

//--------- MAIN URL -----------------------------
private _url: string = "http://127.0.0.1:8040/";
private socket;


constructor( private http: HttpClient ) { this.socket = io(this._url) }
//--------------------------------------------------------


// Monitor
monitor() {
  var socket = io()
  return Observable.create((observer) =>{
    socket.on('/monitor', data => {
      observer.next(data);
    })
  });
}




// Medical Records database
medicalDb() {
  var socket = io()
  return Observable.create((observer) =>{
    socket.on('/medicaldb', data => {
      observer.next(data);
    })
  });
}

//-------------- Open medical file -----------------------------
openmedical( record : any ) {
  return this.http.post<any>(this._url + "openmedical", record, {headers : this.header});
}

//--------------- Reload MED Db ----------------------------
onreloadMED(){
  return this.http.delete<any>(this._url + "medonreload", {headers : this.header});
}

//--------------- Delete File ----------------------------
deleteMED(){
  return this.http.delete<any>(this._url + "deletemedical", {headers : this.header});
}



// User database
userDb() {
  var socket = io()
  return Observable.create((observer) =>{
    socket.on('/userdb', data => {
      observer.next(data);
    })
  });
}

//-------------- Open user Clinician -----------------------
openuserclinician( record : any ) {
  return this.http.post<any>(this._url + "openuserclinician", record, {headers : this.header});
}

//-------------- Open user Admin  -----------------------
openuseradmin( record : any ) {
  return this.http.post<any>(this._url + "openuseradmin", record, {headers : this.header});
}


//--------------- User db reload ----------------------------
onreloadUSER(){
  return this.http.delete<any>(this._url + "useronreload", {headers : this.header});
}

//--------------- Delete USER ----------------------------
deleteUSERCLI(){
  return this.http.delete<any>(this._url + "deleteusercli", {headers : this.header});
}
deleteUSERADM(){
  return this.http.delete<any>(this._url + "deleteuseradm", {headers : this.header});
}




//=============== THE END ================================
}
//------------------------------------------------------
